<template>
  <form class="card" @submit.prevent="onSybmit">
    <h1>Sign in</h1>
    <div :class="[
        'form-control',
        {invalid: eError}
        ]">
      <label for="email">E-mail</label>
      <input type="email" id="email" v-model="email" @blur="eBlur">
      <small v-if="eError">{{eError}}</small>
    </div>
    <div :class="[
        'form-control',
        {invalid: pError}
        ]">
      <label for="password">Password</label>
      <input type="password" id="password" v-model="password" @blur="pBlur">
      <small v-if="pError">{{pError}}</small>
    </div>
    <button class="btn primary" type="submit" :disabled="isSubmitting || isToManyAttempts">Enter</button>
    <span class="text-danger" v-if="isToManyAttempts">Too many attempts, please try again later</span>
  </form>
</template>

<script>
import {useLoginForm} from "../use/login-form";
export default {
    setup() {
      return {...useLoginForm()}
    }
}
</script>

